.content-wrapper{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: rgba(70, 152, 246, 0.1);
}
.content-inner{
    width: 40vh;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    background: rgb(255, 255, 255);
    border-radius: 2em;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.title{
    text-align: center;
    font-size: 2em;
    margin-bottom: 1em;
}
.user-card{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.user-image{
    border-radius: 50%;
    overflow: hidden;
}
.user-name{
    font-size: 1.2em;
}
.outer-wrapper{
    width: 50vw;
    display: flex;
    justify-content: space-between;
}
.orders-outer{
    width: 60vh;
    height: 70vh;
    display: flex;
    justify-content: center;
    /*align-items: center;*/
    flex-direction: column;
    padding: 20px;
    background: rgb(255, 255, 255);
    border-radius: 2em;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.orders-list{
    overflow-y: scroll;
    padding: 15px;
}
.outer-title{
    border-bottom: 1px rgba(0, 0, 0, 0.2) solid;
    padding-bottom: 10px;
}
.user-orders{
    margin-top: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    padding: 10px;
    border-radius: 10px;
}