.orderPass-wrapper{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: rgba(70, 152, 246, 0.1);
}
.order-pass-modal{
    padding: 25px 0;
    width: 50vh;
    /*height: 60vh;*/
    background: white;
    border-radius: 25px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.title{}
.number{}
.ord{
    margin: 15px 20px;
}
.basket-order{
    margin-top: 15px;
}
.basket-order-item{
    margin-bottom: 10px;
    font-size: .9em;
}
.closeBtn{
    background: #e74a4a;
    color: white;
    padding: 15px;
    border-radius: 15px;
    margin-top: 15px;
    cursor: pointer;
}