.order-wrapper{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.empty{
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2em;
}
.order-inner{
    width: 80vw;
    height: 70vh;
    overflow-y: scroll;
    padding: 0;
    background: white;
    border-radius: 25px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.order-info{
    margin-top: 15px;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.address{

}
.address input{
    /*width: 100%;*/
    padding: 5px 10px;
    border-radius: 10px;
}
.pay-way{

}
.pay-title{
    text-align: center;
    margin-bottom: 10px;
}
.pay-ways{
    display: flex;
    justify-content: space-between;
}
.online-pay, .offline-pay{
    display: block;
    text-align: center;
    /*width: 50px;*/
    height: 50px;
    padding: 1em 1em;
    border: 1px solid black;
    border-radius: 10px;
    cursor: pointer;
}
.offline-pay{
    margin-left: 15px;
}
[type="checkbox"]:checked + .online-pay{
    background: #2d45d3;
    color: white;
}
[type="checkbox"]:checked + .offline-pay{
    background: #2d45d3;
    color: white;
}
.order-btn{
    cursor: pointer;
    background: #2d45d3;
    color: white;
    padding: 20px;
    border-radius: 15px;
}