.top-bar{
    height: 100px;
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.basket{
    height: 80%;
    cursor: pointer;
    margin-right: 25px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: white;
    padding: 5px 10px;
    border-radius: 15px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.dot{
    margin-left: 5px;
    width: 25px;
    height: 25px;
    background: red;
    border-radius: 50%;
    text-align: center;
    color: white;
}
.basket img{
    height: 40%;
}
.basket-title{
    display: flex;
    margin-top: 10px;
    font-size: 1.1em;
}
.btn{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
    background: white;
    padding: 15px 30px;
    font-size: 1.2em;
    border-radius: 15px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}