.content-wrapper{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu-wrapper{
    width: 80vw;
    height: 70vh;
    overflow-y: scroll;
    padding: 0;
    background: white;
    border-radius: 25px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.infinite-scroll-component{
    padding: 25px 25px;
}
.menu-item{
    padding: .5em 0;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1em;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}
.image-wrapper{
    margin-left: 15px;
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image-wrapper img {
    border-radius: 25px;
    width: 20em;
}
.props-wrapper{
    width: 65%;
    margin-right: 5%;
    margin-left: 50px;
}
.name{
    margin-top: 2em;
    font-size: 1.2em;
    margin-bottom: 1em;
}
.description{
    margin-bottom: 1em;
}
.price{
    font-size: 1.2em;
    margin-bottom: 1em;
}
.buy{
    display: flex;
    align-items: center;
}
.add, .remove{
    background: #2641dc;
    width: 30px;
    height: 30px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: white;
    font-size: 1.3em;
    cursor: pointer;
}
.quan{
    margin: 0 20px;
}
.delete{
    cursor: pointer;
    margin-left: 25px;
    background: #e74a4a;
    color: white;
    padding: 15px;
    border-radius: 15px;
}